import React from "react";
import { Row, Col, Card, CardBody, Button } from "reactstrap";
import { Link } from "react-router-dom";
const CardLayout = props => (
  <div className="card-marginTop">
    <div className="animated fadeIn">
      <Row className="justify-content-left">
        <Col md="12">
          <Card className="mx-12">
            <CardBody className="p-8">
              <Row className="card-row-padding">
                {props.navigation ? (
                  <Link
                    to={props.navigationRoute}
                    onClick={props.onClick}
                    className="back-arrow-icon"
                  >
                    <i className="fa fa-chevron-circle-left back-arrow-icon" />
                  </Link>
                ) : null}
                <Col xs="12" md="6">
                  {props.name ? (
                    <h1>{props.name}</h1>
                  ) : (
                    <h3>{props.subName}</h3>
                  )}
                </Col>
                <Col md="4" />
                {props.buttonName ? (
                  <Col md="2">
                    {props.gridIcon ? (
                      <i
                        className={props.gridIcon}
                        style={{ fontSize: 20, padding: 9 }}
                        onClick={props.toggleView}
                        title={props.gridIconTitle}
                      />
                    ) : null}
                    <Link to={props.buttonLink} onClick={props.buttonClick}>
                      <Button
                        //title={props.active === "none" ? "Work in progress"  :  props.buttonName}
                        type="button"
                        className="theme-positive-btn card-btn"
                        style={{
                          pointerEvents: props.active,
                          opacity: props.active === "none" ? 0.5 : 1
                        }}
                      >
                        <i className="fa fa-plus" />&nbsp; {props.buttonName}
                      </Button>
                    </Link>
                  </Col>
                ) : null}
              </Row>
              {props.children}
            </CardBody>
          </Card>
        </Col>
      </Row>
    </div>
  </div>
);

export default CardLayout;



